import React, { Component, Fragment } from "react";
import App from "./App";
import Table from "./Table";
import ProductDispaly from "./ProductDisplay";
import ProductAppApi from "../api/SetApi";
class NewComponent extends Component {
  state = {
    id: 1,
    name: "shubham",
    job: "Mst hai",
    data: [
      { id: 1, name: "shubham" },
      { id: 2, name: "shu" },
      { id: 3, name: "shub" },
    ],
    product: [],
  };

  componentDidMount(){
    ProductAppApi.get("/products").then((res) =>{
      console.log(res);
      this.setState({product:res.data});
    })
  }
  render() {
    return (
      <div className="ui container">
        <App />
        <h1>Inside Child Component</h1>
        <h2>{this.props.propsExample + " " + this.props.id}</h2>
        <h2>{this.props.id}</h2>
        <Table name={this.state.name} id={this.state.id} job={this.state.job} />
        {this.state.data.map((e) => {
          return (
            <h4 key={e.id}>
              {e.id} {e.name}
            </h4>
          );
        })}
        {/* {this.state.product.map((product) => {
          return (
            <ProductDispaly
              key={product.id}
              name={product.name}
              prise={product.prise}
              qoh={product.qoh}
            />
          );
        })} */}
        {this.state.product.map((product) => {
          return (
            <ProductDispaly
              key={product.id}
              prod={product}
            />
          );
        })}
      </div>
    );
  }
}

export default NewComponent;
