import React, { Component, Fragment } from "react";
import ProductAppApi from "../api/SetApi";
import ReviewsDisplay from "./ReviewsDisplay";
class ProductDisplay extends Component {
  state = {
    reviews: [],
  };
  showReviews = () => {
    ProductAppApi.get("/products/" + this.props.prod.id + "/reviews").then(
      (res) => {
        console.log(res);
        this.setState({ reviews: res.data });
      }
    );
    <ReviewsDisplay review={this.state.reviews} />;
  };
  render() {
    let product = this.props.prod;
    return (
      <Fragment>
        <div class="ui card">
          <div class="content">
            <a class="header">{product.name}</a>
            <div class="description">Price: {product.prise}</div>
            <div class="description">Quantity On Hand: {product.qoh}</div>
            <div class="description">
              <button
                class="ui positive basic button"
                onClick={this.showReviews}
              >
                Review
              </button>
            </div>
            <div className="extra content">
              {this.state.reviews.map((reviews) => {
                return <ReviewsDisplay key={reviews.id} review={reviews} />;
              })}
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default ProductDisplay;
